.product {
  overflow: hidden;
  border-radius: 4px;

  &__buttons {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.6);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 80px;
    height: 80px;
    background-color: var(--color-main);
    transition: background-color 0.3s ease-in-out;

    svg {
      width: 30px;
      height: 30px;
      fill: var(--color-white);
      object-fit: cover;
    }

    &:first-child {
      margin-right: var(--main-offset);
    }

    &--disabled {
      opacity: 0.4;
      pointer-events: none;
    }

    @include desktop {
      &:hover,
      &:focus {
        background-color: var(--color-main-hover);
      }
    }
  }

  &__image {
    position: relative;
    margin-bottom: var(--small-offset);
    width: 100%;
    max-height: 293px;

    img {
      display: block;
      width: 100%;
      height: 293px;
      object-fit: cover;
    }
  }

  &__title {
    display: flex;
    display: box;
    overflow: hidden;
    margin: 0 0 var(--small-offset);
    font: var(--middle-book);
    color: var(--color-main);
    box-orient: vertical;
    -webkit-line-clamp: 1;
  }

  &__price {
    font: var(--prehigh-regular);
    color: var(--color-main);
  }


  &:hover,
  &:focus {
    .product__buttons {
      opacity: 1;
      visibility: visible;
    }
  }

}

.mini-product {
  display: flex;
  align-items: center;
  padding: var(--small-offset) var(--second-offset);

  &__image {
    flex-shrink: 0;
    margin-right: var(--small-offset);
    width: 100px;
    height: 100px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile {
      width: 60px;
      height: 60px;
    }
  }

  &__content {
    display: flex;
    flex-grow: 1;
    align-items: center;
  }

  &__text {
    margin-right: auto;
    max-width: 268px;
  }

  &__title {
    margin: 0 0 14px;
    font: var(--small-book);
    line-height: 20px;
    color: var(--color-main);
  }

  &__price {
    font: var(--prehigh-regular);
    font-weight: 700;
    color: #4d4d4d;
  }

  &__delete {
    width: 20px;
    height: 20px;
    font-size: 0;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      pointer-events: none;
    }

    @include mobile {
      flex-shrink: 0;
    }
  }
}
